
import { defineComponent, ref } from 'vue';
import gameService from '../lib/game-service';

export default defineComponent({

   name: 'GamePlayerQuestion',

   setup: () => {
      const answer = ref('');

      return {
         answer,
         game: gameService.game,

         submitAnswer: () => {
            gameService.submitAnswer(answer.value);
         },
      };
   },

});
